body {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
}

.photo-card {
  width: calc(25% - 20px);
  max-height: 400px;
  border: 1px solid #e7e9fc;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.image-container {
  flex: 1;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.info {
  flex-direction: row;
  display: flex;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.info-item {
  text-align: center;
}

.search-form {
  max-width: 400px;
  margin: 10px auto;
  display: flex;
}

.search-form input {
  border: 1px solid #ccc;
  border-radius: 4px 0 0 4px;
  flex: 1;
  padding: 8px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: 1px solid #007bff;
  border-radius: 0 4px 4px 0;
  padding: 8px 12px;
  font-size: 16px;
}

.search-form button:hover, .search-form button:focus {
  background-color: #0056b3;
  border-color: #0056b3;
}

.is-hidden {
  display: none;
}

